import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "src/app/core/services";
import { CategoriesService } from "../categories.service";


@Component({ 
    templateUrl: './categories.create.template.html',
    styleUrls: ['./categories.create.styles.scss']
})
export class CategoriesCreateComponent implements OnInit {
    		
	public categoryForm: FormGroup;
	public submitted: boolean;
	loaded:boolean;

    constructor(
		private authentication: AuthenticationService,
		private categoryService: CategoriesService,
		private formBuilder: FormBuilder,
		private toast: ToastrService,
	 	private router: Router, 
    ){}

    
    ngOnInit(){
        this.loaded = true;
        this.categoryForm = this.formBuilder.group({
            title: ['', [Validators.required]] 
        });
    }


	create() {
		
		this.submitted = true;
		if (!this.categoryForm.valid) { 
			//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
			return;
		}

	
		let data = {
            "slug": this.categoryForm.get("title").value.split(' ').join(''),  
            "name": this.categoryForm.get("title").value,
            "parent": null,
            "perms": [""]
        }

		this.categoryService.createCategory(data).then((data) => {
                console.log(data);
                this.router.navigate(['redactor/categories'])
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}


}
