import { Component, OnInit } from "@angular/core";
import { Validators, FormBuilder, FormGroup } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { CategoriesService } from "../categories.service";
import { AuthenticationService } from "src/app/core/services";


@Component({ 
    templateUrl: './categories.edit.template.html',
    styleUrls: ['./categories.edit.styles.scss']
})
export class CategoriesEditComponent implements OnInit {

    public categoryForm: FormGroup;
    public categoryId;
	public submitted: boolean;
	loaded:boolean;

    constructor(
		private authentication: AuthenticationService,
		private categoryService: CategoriesService,
		private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private toast: ToastrService,
        private router: Router, 
    ){}

    
    async ngOnInit(){
        await this.route.paramMap.subscribe(params => { this.categoryId = params.get("categoryId") });
        await this.categoryService.getCategory(this.categoryId).then(data => {
            console.log(data);
            this.loaded = true;
            this.categoryForm = this.formBuilder.group({
                title: [data.info.name, [Validators.required]] 
            });

        })

    }


	create() {
		
		this.submitted = true;
		if (!this.categoryForm.valid) { 
			//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
			return;
		}

	
		let data = {
            "slug": this.categoryForm.get("title").value.split(' ').join(''),  
            "name": this.categoryForm.get("title").value,
            "parent": null,
            "perms": [""]
        }

		this.categoryService.editCategory(this.categoryId,data).then((data) => {
                console.log(data);
                this.router.navigate(['redactor/categories'])
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}

 


}
