import { Routes } from "@angular/router";
import { CategoriesComponent } from "./categories.component";
import { CategoriesCreateComponent } from "./create/categories.create.component";
import { CategoriesEditComponent } from "./edit/categories.edit.component";

/**
 *  
 * 
 * 
 */


// Actual moudule routes
export const categories_routes: Routes = [ 
    {
        path: 'categories',
        loadChildren: 'src/app/common/modules/categories/categories.module#CategoriesModule'
    }
] 

// Required for lazy-loading
export const routes: Routes = [ 
    {
        path: '',
        component: CategoriesComponent
    },
    {
        path: 'create',
        component: CategoriesCreateComponent
    },
    {
        path: 'edit/:categoryId',
        component: CategoriesEditComponent
    },
]
